/* #region Emblem-Card */
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
/* #endregion */

.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 90vh;            /* full viewport height */
  text-align: center;
}

.logo {
  margin: 1rem 0;
  height: 20vh;
  padding: 10vh 0vh;
}

.logo img {
  max-width: 100%;
  height: auto;
  max-height: 200px;
}
